<template>
  <div class="my-task view-list">
    <div class="header-sub">
      <div class="title">
        <div class="breadcrumb-item-link">任务列表</div>
        <span class="empty"></span>
        <span class="t-btn">

        </span>
      </div>
    </div>
    <div class="content">
      <div class="form-ops">
        <Button :type="tabTxt=='0'?'primary':'default'" style="margin-right: 20px" @click="selectTab('0')">{{$t_p('upcoming')}}</Button>
        <Button :type="tabTxt=='1'?'primary':'default'" @click="selectTab('1')">{{$t_p('has_done')}}</Button>
      </div>
      <div class="con" v-show="tabTxt=='0' ">
        <div class="user-table no-scroll">
          <Table
            class="m-table no-border no-scroll"
            :loading="isQuerying"
            :row-class-name="(row,index)=>index%2===1?'even':''"
            stripe
            :columns="tableColumns"
            :data="tableData"
          ></Table>
        </div>
        <div class="pagination">
          <Page
            @on-change="onPaginationChange"
            @on-page-size-change="onPaginationSizeChange"
            :current="pageInfo.pageIndex"
            :page-size="pageInfo.pageSize"
            :total="pageInfo.totalElements"
            show-sizer
            show-total
          />
        </div>
      </div>
      <div class="con" v-show="tabTxt=='1' ">
        <div class="user-table ">
          <Table
            class="m-table no-scroll no-border"
            :loading="isQueryingAll"
            :row-class-name="(row,index)=>index%2===1?'even':''"
            stripe
            :columns="tableColumns1"
            :data="tableDataAll"
          ></Table>
        </div>
        <div class="pagination">
          <Page
            @on-change="onPaginationChange1"
            @on-page-size-change="onPaginationSizeChange1"
            :current="pageInfoAll.pageIndex"
            :page-size="pageInfoAll.pageSize"
            :total="pageInfoAll.totalElements"
            show-sizer
            show-total
          />
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import {getTaskList} from '../../../../api/task'

  export default {
  name: "my-task",
    i18n_prefix:'page.message-center.my-task',
  data() {
    return {
      tableColumns: [
        {
          title: this.$t_p('task_name'),
          key: "worderName",
          align: "center"
        },
        {
          title: this.$t_p('founder'),
          key: "creatorName",
          align: "center"
        },
        {
          title: this.$t_p('commit_time'),
          key: "createTime",
          align: "center"
        },
        {
          title: this.$t('common.text.operate'),
          key: "action",
          width: 200,
          align: "center",
          render: (h, params) => {
            return h("div", [
              this.$hasAccess("forder.query")  &&   this.$hasAccess("border.query") &&
              h(
                "span",
                {
                  props: {
                    type: "text",
                    size: "small"
                  },
                  class:"primary",
                  on: {
                    click: () => {
                      this.editInfo(params);
                    }
                  }
                },
                this.$t_p('handle')
              )
            ]);
          }
        }
      ],
      tableColumns1: [
        {
          title: this.$t_p('task_name'),
          key: "worderName",
          align: "center"
        },
        {
          title: this.$t_p('founder'),
          key: "creatorName",
          align: "center"
        },
        {
          title: this.$t_p('commit_time'),
          key: "createTime",
          align: "center"
        },
        {
          title: this.$t('common.text.operate'),
          key: "action",
          width: 200,
          align: "center",
          render: (h, params) => {
            return h("div", [
             this.$hasAccess("forder.query")  &&   this.$hasAccess("border.query")  &&
              h(
                "span",
                {
                  props: {
                    type: "text",
                    size: "small"
                  },
                  class:"primary",
                  on: {
                    click: () => {
                      this.editInfo1(params);
                    }
                  }
                },
                this.$t('common.message.detail')
              )
            ]);
          }
        }
      ],
      tableData: [],
      pageInfo: {
        pageIndex: 1,
        pageSize: 10,
        totalElements: 0
      },
      isQuerying: false,
      tableDataAll: [],
      pageInfoAll: {
        pageIndex: 1,
        pageSize: 10,
        totalElements: 0
      },
      isQueryingAll: false,
      tabTxt: "0"
    };
  },
  methods: {
    loadTableData(clear) {
      if (clear) {
        this.pageInfo.pageIndex = 1;
      }
      this.pageInfo.loading = true;

      getTaskList({
        pageNumber: this.pageInfo.pageIndex,
        pageSize: this.pageInfo.pageSize,
        handled: 2
      })
        .then(res => {
          this.tableData = (res.data.result || []).map(d => ({
            ...d
          }));
          this.pageInfo.totalElements = res.data.totalElements || 0;
        })
        .finally(() => {
          this.pageInfo.loading = false;
        });
    },
    loadTableDataAll(clear) {
      if (clear) {
        this.pageInfoAll.pageIndex = 1;
      }
      this.pageInfoAll.loading = true;
      let taskType;
      getTaskList({
        pageNumber: this.pageInfoAll.pageIndex,
        pageSize: this.pageInfoAll.pageSize,
        handled: 1,
        // taskType: taskType
      })
        .then(res => {
          this.tableDataAll = (res.data.result || []).map(d => ({
            ...d
          }));
          this.pageInfoAll.totalElements = res.data.totalElements || 0;
        })
        .finally(() => {
          this.pageInfoAll.loading = false;
        });
    },
    onPaginationChange(index) {
      this.pageInfo.pageIndex = index;
      this.loadTableData(false);
    },
    onPaginationChange1(index) {
      this.pageInfoAll.pageIndex = index;
      this.loadTableDataAll(false);
    },
    onPaginationSizeChange(size) {
      this.pageInfo.pageSize = size;
      this.loadTableData(true);
    },
    onPaginationSizeChange1(size) {
      this.pageInfoAll.pageSize = size;
      this.loadTableDataAll(true);
    },
    editInfo(params) {
      //1故障2保养
      //   console.log(123,params,this.tableData);
      let uInfo = this.tableData[params.index];
      if (uInfo.taskType == 2) {
        this.$router.push({
          name: "fault-work-detail",
          params: { id: uInfo.worderId }
        });
      } else {
        this.$router.push({
          name: "maintenance-work-detail",
          params: { id: uInfo.worderId }
        });
      }
    },
    editInfo1(params) {
      //1故障2保养
      let uInfo = this.tableDataAll[params.index];
      if (uInfo.taskType == 2) {
        this.$router.push({
          name: "fault-work-detail",
          params: { id: uInfo.worderId }
        });
      } else {
        this.$router.push({
          name: "maintenance-work-detail",
          params: { id: uInfo.worderId }
        });
      }
    },
    selectTab(txt) {
      this.tabTxt = txt;
    }
  },
  mounted() {
    this.loadTableData(true);
    this.loadTableDataAll(true);
    // this.loadTableDataNew(true)
    // this.loadTableDataAllNew(true);
  }
};
</script>

<style lang="less">
@import "../../../../style/varient";
.user-table {
}
</style>

<style lang="less" scoped>
@import "../../../../style/varient";
@size-padding: 10px;

</style>



